    import Header from "./components/Header";
    import PostList from "./components/PostList";
    // import Article from "./components/Article";

    export default {
        name: "App",
        components: {
            Header,
            PostList
        }
    };
</script>

<style>
  * {
    margin: 0;
    padding: 0;
  }

  .main {
    width: 80%;
    margin: 0 auto;

  }
  #app{
    background:#e1e1e1 ;
  }
  a{
    text-decoration: none;
  }
</style>



